<script>
import { defineComponent, watch, computed } from 'vue'
import debounce from 'utils/debounce'
import { useConditionAll } from 'composable/setting'
import { processing as processingPriceImport } from 'composable/pricing'
import Tab from 'bbUi/components/tab/tab.vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Pagination from 'components/Pagination.vue'

import { sanitize } from 'utils/dom'
import { useRouter } from 'vue-router'

export default defineComponent({
  components: {
    Tab,
    Button,
    Card,
    Input,
    Badge,
    Modal,
    Breadcrumb,
    Pagination
  },
  async setup(props, { emit }) {
    const router = useRouter()
    const {
      data,
      state,
      stateSelectedMoreDevice,
      isAvailableDevice,
      loading,
      modal,
      selected,
      unsetData,
      openModal,
      actionAll,
      actionDelete,
      actionMoreDevice,
      actionConditionDeviceAvailability
    } = useConditionAll()

    const priceUploadProcess = computed(() =>
      processingPriceImport.value.uploadProgress > 0 &&
      processingPriceImport.value.uploadProgress < 100
    )
    const openModalSelected = (target, item) =>
      openModal(target, () => {
        selected.value = item.id
      })

    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Conditions'
      }
    ]

    const handleShowMoreDevice = async (condition = null) => {
      if (condition) {
        await actionMoreDevice(
          condition.id,
          {
            is_price_sync: condition.is_price_sync
              ? condition.is_price_sync
              : 0,
            unique_id: condition.unique_id ? condition.unique_id : null
          },
          () => {
            openModal('more_device')
          }
        )
      }
    }
    const handleSearch = debounce(async () => {
      await Promise.all([actionAll(), actionConditionDeviceAvailability()])
    }, 300)
    const rowClick = (row, _, el) => {
      if (priceUploadProcess.value) return

      const pathTo = {
        name: 'settingConditionEdit',
        params: { conditionId: row.id },
        query: {
          ...(row.is_price_sync
            ? {
                price_sync: 1,
                unique_id: row.unique_id
                  ? row.unique_id
                  : ''
              }
            : row.unique_id && {
              unique_id: row.unique_id 
            }) || {}
        }
      }
      if (el.metaKey || el.ctrlKey) {
        window.open(`${router.resolve(pathTo).href}`, '_blank')
        //   console.log("Command/Ctrl key is pressed!");
        //   // Your logic here when Command/Ctrl is pressed
      } else router.push(pathTo)
    }
    const cellMouseEnter = (row, col, el, e) => {
      // if (!el.title.length) el.title = 'Go to detail'
    }
    const cellMouseLeave = (row, col, el, e) => {
      // if (el.title.length) el.title = ''
    }

    watch(
      () => ({
        page: data.page,
        limit: data.limit
      }),
      async (valNew, valOld) => {
        await handleSearch()
      },
      { deep: true }
    )

    await Promise.all([actionAll(), actionConditionDeviceAvailability()])

    return {
      cellMouseEnter,
      cellMouseLeave,
      rowClick,
      data,
      state,
      stateSelectedMoreDevice,
      isAvailableDevice,
      loading,
      modal,
      breadcrumb,
      unsetData,
      actionDelete,
      openModalSelected,
      handleShowMoreDevice,
      handleSearch,
      sanitize,
      priceUploadProcess
    }
  }
})
</script>
